
:root{
  --mainBlue: #3594FF;
}

.carouselImg{
    width: 20rem;
    height: 13rem;
    border-radius: 10px;
    object-fit: cover;
}
.cardImg{
    /* border: 2px solid var(--mainBlue) ; */
    display: inline-block;
    padding-bottom: 1rem;
    z-index: -1;
    overflow: hidden;
}
.cardbelow{
display: flex;
margin: 1rem;
}
.cardtext{
    margin-left: 2rem;
    text-align: left;
}

@media(max-width:600px){
    .carouselImg{
        width: 16rem;
    }
}