:root{
    --mainBlue: #3594FF;
  }


.background{
    width: 100%;
    height: auto;
    background-color: var(--mainBlue);
    position: relative;
    display: flex;
    flex-wrap: wrap;
}
.imgText{
    width: 50%;
    color: white;
}
.imgText h1{
    padding: 2rem;
}
.catlist{
    display: flex;
    flex-wrap: wrap;

}
.catlist li a {
    text-decoration: none;
    color: white;
 
}
.catlist ul{
    list-style: none;
}
.catlist li{
       padding: 1rem;
       margin-right: 3rem;
}
.catlistParent{
    display: flex;
    flex-wrap: wrap;
}










@media(max-width:900px){
    .imgText{
        width: 100%;
        text-align: center;
    }
    .imgText h1{
        font-size: 3rem;
    }
    .catlistParent{
       
        justify-content: center;
    }
}
@media(max-width:1630px){
    .imgText{
        width: 100%;
    }
    .background{
        display: flex;
        text-align: center;
        justify-content: center;
        align-items: center;
    }
    
    .catlist li{
        margin: 0;
    }
  
}

