.bestTalent{
  z-index: 99;
  padding: 3rem;
  display: flex;
  flex-direction: column;
  margin-top: 15rem;
  text-align: center;
}
.bestTalent h1{
  color: rgb(43, 43, 43);
}

.btncenter{
  width: 100%;
  display: flex;
  justify-content: center;
  padding: 2rem 0 2rem 0;
}

.blogs{
  position: relative;
  display: inline-block;
  width: 100%;
}
.blogs h1{
  padding: 2rem;
}
.postimg{
  width: 100%;
  display: block;
  position: absolute;
  height: 100%;
}
.post1{
  max-width:15rem;
  padding: 2rem;
  margin: 2rem;
 
}
.blogs h1{ 
  position: relative;
 
}
.post1 h2 p{
  position: absolute;
}
.post1 h2{
  color: var(--mainBlue);
}
.post1{

  transition: all 0.3s ease;
}
.post1:hover{
  cursor: pointer;
  background-color: rgb(240, 239, 239);
  /* border: 1px solid var(--mainBlue); */
}
.postwrap{
  position: relative;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.businesscards{
  width: 100%;
}
.businesscards{
  display: flex;
  justify-content: center;
  width: 100%;
  flex-wrap: wrap;
}
.businesscards .cardImg{
  max-width: 25rem;
  margin: 2rem;
}
.article h1{ 
  padding: 2rem;
 
}
.article{
  display: block;
  width: 100%;
  
}
.articlepost{
  width: 20rem;
  height: auto;
  padding: 2rem;
}
.articlepost p{
  margin-bottom: 2rem;
}
.articleParent{
  display: flex;
  width: 100%;
  flex-wrap: wrap;
  justify-content: space-around;
}



  @media(max-width:900px){
    .mainImgSearch{
        top: 0;
    }
    
    .container1 img{
      content: url("../components//images/Artboard2.png")
    }
    .popbox img{
        width: 12rem;
    }
.article {
  text-align: center;
}
    .bannertext img{ 
      width: 10rem;
      height: auto;
      padding: 1rem;
    }


  }

@media (max-width: 1300px){
    .bannerimg{
      content: url("../components/images/banner3.png")
    }
  }

  @media(max-width:600px){
    .bannertext{
      width: 80%;
    }
.searchcont{
  margin-top: 2rem;
}
.mainImgSearch h1{
  margin-top: 7rem;
  font-size: 2.2rem;
}

    .popbox img{
      margin: 1rem;
      width: 7rem;
    }

    .bestTalent{
      margin-top: 5rem;
    }
  }
  
