:root{
    --mainBlue: #3594FF;
  }

.bluebutton{
    width: 10rem;
    height: 3rem;
    background-color: var(--mainBlue);
    border: none;
    color: white;
    border-radius: 10px;
    transition: all 0.5s ease;

}

.bluebutton:hover{
    cursor: pointer;
    width: 12rem;
    background-color: white;
    color: var(--mainBlue);
    border: 2px solid var(--mainBlue);
    font-weight: bold;
}